.btn {
  display: inline-block;
  background: #333333;
  color: #fff;
  padding: 0.4rem 1.3rem;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  margin-right: 0.5rem;
  transition: opacity 0.2s ease-in;
  outline: none;
  &:disabled {
    background: #ccc;
    color: #000;
    cursor: not-allowed;
  }
  &:hover {
    opacity: 0.8;
  }
}